.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo {
  object-fit: cover;
  width: 40px;
  height: 40px;
}

.nav__menu-list {
  padding-inline-start: 0;
  margin: 0;
  padding: 0;
  display: flex;
}

.nav__items {
  align-items: center;
  margin-right: 20px;
  list-style: none;
  display: flex;
  position: relative;
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: block;
  position: relative;
}

.nav__link:hover {
  color: #00acdc;
}

li:nth-of-type(8) {
  margin-right: 0;
}

.is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: flex;
  position: absolute;
  bottom: 0;
  left: 0;
}

* {
  margin: 0;
}

html {
  color: #060b35;
  scroll-behavior: smooth;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.container {
  --column-count: 1;
  grid-template-columns: repeat(var(--column-count), 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .container {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .container {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .container {
    --column-count: 4;
  }
}

.product {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  max-width: 200px;
  margin: 0 auto;
  transition: all, all .3s;
}

.product__content--paddings {
  padding-bottom: 24px;
}

.product__card {
  padding: 0 19px;
}

.product__card--padings {
  padding-top: 32px;
}

.product__title {
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.product__title--paddings {
  padding-bottom: 4px;
}

.product:hover .product__title {
  color: #34568b;
}

.product:hover {
  transform: scale(1.2);
}

.product__image {
  background-image: url("imac.202c52ff.jpeg");
  background-repeat: no-repeat;
  background-size: 160px 134px;
  height: 134px;
}

.product__image--paddings {
  padding-bottom: 40px;
}

.product__container {
  margin: 0 auto;
}

.product__container--paddings {
  padding: 0 16px 16px;
}

.product__description {
  color: #616070;
  display: flex;
}

.product__description--paddings {
  padding-bottom: 16px;
}

.product__reviews {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.product__price {
  justify-content: space-between;
  display: flex;
}

.product__price--paddings {
  padding-bottom: 16px;
}

.product__key {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.product__value {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.product__btn {
  text-align: center;
  text-transform: uppercase;
  cursor: pointer;
  color: #fff;
  padding-inline: 0;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  width: 100%;
  padding: 12px 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
}

.product__btn:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: 50%;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
  display: flex;
}

.stars--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

body {
  margin: 0;
}
